import request from './request'

export function currentUrlToParams(key = null) {
    let paramsUrl = (window.location.href).split('?');
    if (paramsUrl.length < 2) return key ? null : {};
    let paramsArr = paramsUrl[1].split('&');
    let paramsData = {}
    paramsArr.forEach(r => {
        let data = r.split('=')
        paramsData[data[0]] = data[1]
    })
    if (key) return paramsData.hasOwnProperty(key) ? paramsData[key] : null;
    return paramsData;
}

export function clone(obj) {
    let strData = JSON.stringify(obj)
    return JSON.parse(strData);
}

export function fetch(api, param, cb, type = 'post') {
    if (!sessionStorage.deviceId) {
        sessionStorage.deviceId = ('44kf00-s003-' + new Date().getTime()).substring(0, 26)
    }
    let DEFAULT = {
        deviceId: sessionStorage.deviceId
    }
    DEFAULT = Object.assign({}, DEFAULT, param)
    let callBack = installBC(cb)
    request({
        url: api,
        method: type,
        data: DEFAULT
    }).then(data => {
        callBack['success'](data)
    }, function (reason, data) {
        callBack['error'](reason, data)
    })
}

export function fetch_get(api, param, cb) {
    if (!sessionStorage.deviceId) {
        sessionStorage.deviceId = ('44kf00-s003-' + new Date().getTime()).substring(0, 26)
    }
    let callBack = installBC(cb)
    request({
        url: api,
        params: param
    }).then(data => {
        callBack['success'](data)
    }, function (reason, data) {
        callBack['error'](reason, data)
    })
}

function installBC(cb) {
    let func = {}
    if (!cb) {
        func = {
            success() { },
            error() { }
        }
    } else if (typeof cb === 'function') {
        func.success = cb
    } else {
        func = cb
    }

    if (!func['error']) {
        func['error'] = function () { }
    }
    return func
}


