<template>
  <div class="customer-orders">
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 用户信息-->
        <el-tab-pane label="用户信息" name="first">
          <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
              <el-form-item label="手机号码">
                <el-input v-model="xx.accountNumber" placeholder="请输入手机号码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" icon="el-icon-search" @click="queryXxData">查询</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <!--列表-->
          <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <div class="show-detail">
              <div class="pp-item" v-for="(xxArray, i) in xx.xxResult" :key="i">
                <div class="dk-detail flex flex-item" v-for="(item, index) in xxArray" :key="index">
                  <span style="width: 200px">{{item.key}}</span>
                  <span style="margin-left:50px">{{item.value}}</span>
                </div>
              </div>
            </div>
          </el-col>
        </el-tab-pane>
        <!-- 代扣订单-->
        <el-tab-pane label="代扣订单" name="second">
          <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
              <el-form-item label="手机号码">
                <el-input v-model="dk.mobile" placeholder="请输入手机号码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" icon="el-icon-search" @click="queryDkData">查询</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <!--列表-->
          <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <div class="show-detail">
              <div class="dk-detail flex flex-item" v-for="(item, index) in dk.dkResult" :key="index">
                <span style="width: 200px">{{item.key}}</span>
                <span style="margin-left:50px">{{item.value}}</span>
              </div>
            </div>
          </el-col>
        </el-tab-pane>
        <!-- 跑批订单-->
        <el-tab-pane label="跑批订单" name="third">
          <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
              <el-form-item label="跑批订单号">
                <el-input v-model="pp.payOrderNo" placeholder="请输入订单号"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" icon="el-icon-search" @click="queryPpData">查询</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <!--列表-->
          <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <div class="show-detail">
              <div class="pp-item" v-for="(ppArray, i) in pp.ppResult" :key="i">
                <div class="dk-detail flex flex-item" v-for="(item, index) in ppArray" :key="index">
                  <span style="width: 200px">{{item.key}}</span>
                  <span style="margin-left:50px">{{item.value}}</span>
                </div>
              </div>
            </div>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script type="text/ecmascript-6">
  import {
    fetch
  } from '@/utils'
  import * as types from '@/api'
  export default {
    data() {
      return {
        activeName: 'first',
        dk: {
          mobile: '',
          dkResult: []
        },
        pp: {
          payOrderNo: '',
          ppResult: []
        },
        xx: {
          accountNumber: '',
          xxResult: []
        }
      }
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      queryDkData() {
        let self = this
        if (!self.dk.mobile) {
          this.$message({
            message: '请输入手机号！',
            type: 'warning'
          });
          return
        }
        fetch(types.QUERY_DK, {
          mobile: self.dk.mobile
        }, {
          success: res => {
            let data = res.data
            self.dk.dkResult = []
            for (let key in data) {
              self.dk.dkResult.push({
                key,
                value: data[key]
              })
            }
          },
          error: res => {
            self.dk.dkResult = []
            this.$message.error('接口请求异常！')
          }
        })
      },
      queryPpData() {
        let self = this
        if (!self.pp.payOrderNo) {
          this.$message({
            message: '请输入订单号！',
            type: 'warning'
          });
          return
        }
        fetch(types.QUERY_PP, {
          payOrderNo: self.pp.payOrderNo
        }, {
          success: res => {
            let data = res.data
            self.pp.ppResult = []
            data.forEach(item => {
              let array = []
              for (let key in item) {
                array.push({
                  key,
                  value: item[key]
                })
              }
              self.pp.ppResult.push(array)
            })
          },
          error: res => {
            self.pp.ppResult = []
            this.$message.error('接口请求异常！')
          }
        })
      },
      queryXxData() {
        let self = this
        if (!self.xx.accountNumber) {
          this.$message({
            message: '请输入手机号！',
            type: 'warning'
          });
          return
        }
        fetch(types.QUERY_USER, {
          accountNumber: self.xx.accountNumber
        }, {
          success: res => {
            let data = res.data
            self.xx.xxResult = []
            data.forEach(item => {
              let array = []
              for (let key in item) {
                array.push({
                  key,
                  value: item[key]
                })
              }
              self.xx.xxResult.push(array)
            })
            console.log(JSON.stringify(self.xx))
          },
          error: res => {
            self.xx.xxResult = []
            this.$message.error('接口请求异常！')
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .customer-orders {
    position: relative;
  }
  .pice-one {
    position: relative;
    padding: 20px;
  }
  .show-detail {
    position: relative;
  }
  .pp-item {
    margin-bottom: 20px;
    border: 1px #999999 solid;
    padding: 15px;
  }
  .dk-detail {
    height: 30px;
  }
</style>


